section.side-bar {
    background-color: #fff;
    border-top: 1px solid #dbdbdb;
    bottom: 0;
    display: flex;
    flex-direction: row;
    height: 50px;
    position: fixed;
    width: 100%;
    z-index: 1112;


    nav.main-navbar {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        width: 100%;
        gap: 10px;

        a.home-nav-bar {
            align-items: center;
            cursor: pointer;
            display: flex;
            font-family: Segoe UI, sans-serif;
            text-decoration: none;
            width: 30px;
            font-size: 22px;
            justify-content: center;
            padding: 5px;
            position: relative;
        }

        a.search-nav-bar {

            align-items: center;
            color: #262626;
            cursor: pointer;
            display: flex;
            font-family: Segoe UI, sans-serif;
            text-decoration: none;
            width: 30px;
            font-size: 22px;
            justify-content: center;
            padding: 5px;
            position: relative;

            span.search-icon {
                font-size: 22px;
                position: relative;
                width: 30px;
                display: flex;
            }
        }

        a.explore-nav-bar {
            align-items: center;
            color: #262626;
            cursor: pointer;
            display: flex;
            font-family: Segoe UI, sans-serif;
            text-decoration: none;
            width: 30px;
            font-size: 22px;
            justify-content: center;
            padding: 5px;
            position: relative;

            span.nav-span {
                font-size: 22px;
                position: relative;
                width: 30px;
                display: flex;
            }
        }

        a.reels-nav-bar {
            align-items: center;
            color: #262626;
            cursor: pointer;
            display: flex;
            font-family: Segoe UI, sans-serif;
            text-decoration: none;
            width: 30px;
            font-size: 22px;
            justify-content: center;
            padding: 5px;
            position: relative;

            span.nav-span {
                font-size: 22px;
                position: relative;
                width: 30px;
                display: flex;
            }
        }

        a.messages-nav-bar {
            align-items: center;
            color: #262626;
            cursor: pointer;
            display: flex;
            font-family: Segoe UI, sans-serif;
            text-decoration: none;
            width: 30px;
            font-size: 22px;
            justify-content: center;
            padding: 5px;
            position: relative;

            span.nav-span {
                font-size: 22px;
                position: relative;
                width: 30px;
                display: flex;
            }
        }

        a.like-nav-bar {
            align-items: center;
            color: #262626;
            cursor: pointer;
            display: flex;
            font-family: Segoe UI, sans-serif;
            text-decoration: none;
            width: 30px;
            font-size: 22px;
            justify-content: center;
            padding: 5px;
            position: relative;

            span.nav-span {
                font-size: 22px;
                position: relative;
                width: 30px;
                display: flex;
            }
        }

        a.create-nav-bar {
            align-items: center;
            color: #262626;
            cursor: pointer;
            display: flex;
            font-family: Segoe UI, sans-serif;
            text-decoration: none;
            width: 30px;
            font-size: 22px;
            justify-content: center;
            padding: 5px;
            position: relative;

            span.nav-span {
                font-size: 22px;
                position: relative;
                width: 30px;
                display: flex;
            }
        }
        a.profile-nav-bar {
            align-items: center;
            color: #262626;
            cursor: pointer;
            display: flex;
            font-family: Segoe UI, sans-serif;
            text-decoration: none;
            width: 30px;
            font-size: 22px;
            justify-content: center;
            padding: 5px;
            position: relative;

            span.nav-span {
                font-size: 22px;
                position: relative;
                width: 30px;
                display: flex;
            }
        }

       section.more-menu-container {
            align-items: center;
            color: #262626;
            cursor: pointer;
            display: flex;
            font-family: Segoe UI, sans-serif;
            text-decoration: none;
            width: 30px;
            font-size: 22px;
            justify-content: center;
            padding: 5px;
            position: relative;

            span.nav-span {
                font-size: 22px;
                position: relative;
                width: 30px;
                display: flex;
            }
        }

        .navbar-item.profile img {
            border-radius: 50%;
            height:30px;
            width:30px;
        }
    }
    a.nav-more-btn{
        width:5px;
        position: relative;
        left : -5vw;
        bottom:-5vw;
        padding: auto;

        svg{
            padding-left:0px;
        }
    }

}







